<template>
<main-layout :backgroundImage="backgroundURL" backgroundOffset="10">

  <template #header>
    <div class="module-section">
      <div class="module-navbar">
        <router-link to="modules" class="link">
          <span class="material-icons">arrow_back</span>&nbsp;
          Назад до списку курсів
        </router-link>
        <span class="link" @click="isMinimized = isMinimized.map(_ => allMinimized ? true : false)">
          {{ allMinimized ? 'Розгорнути все' : 'Згорнути все' }}&nbsp;
          <span class="material-icons">{{ allMinimized ? 'expand_more' : 'expand_less' }}</span>
        </span>
      </div>
      <div class="module-course line-r">НМТ-2022</div>
      <div class="module-name">{{ moduleName }}</div>
      <div class="bar-wrapper">
        <bar-horizontal :data="[totalScore, totalScoreMax - totalScore]" :colors="['#fffa','#fff4']" />
      </div>
    </div>
  </template>

  <template #body>
    <div class="block-section" v-for='(block, i) in blocks' :key="block.name">
      <div class="block-title" @click="isMinimized[i] = !isMinimized[i]">
        <span class="block-name">ТЕМА {{ i+1 }}.&ensp;{{ block.name }}</span> 
        <span class="material-icons">{{ isMinimized[i] ? 'expand_less' : 'expand_more' }}</span>
      </div>
      <div class="block-line" v-if="isMinimized[i]"></div>
      <div class="block-lesson" v-if="isMinimized[i]">
        <lesson-card v-for='(lesson, j) in block.lessons' :key='lesson.name' 
         :lesson='{ ...lesson, index: j+1, blockIndex: i+1 }' 
         @click='launchQuiz(block.name, block._id, lesson.name, lesson._id)'/>
      </div>
    </div>
  </template>

</main-layout>
</template>

<script>
import mainLayout from '@/layouts/main-layout.vue'
import lessonCard from '@/components/lesson-card.vue'
import barHorizontal from '@/components/bar-horizontal.vue'
export default {
  components: { mainLayout, lessonCard, barHorizontal },
  data() { return {
    isMinimized: this.$store.state.blocks.map(_ => true),
    moduleName: localStorage.getItem('moduleName'),
    moduleId: localStorage.getItem('moduleId'),
  }},
  computed: {
    backgroundURL() { return this.$cloudinary + this.moduleId + '.jpg' },
    allMinimized() { return !this.isMinimized.includes(true) },
    blocks() { return this.$store.state.blocks },
    totalScore() { return this.blocks.map(block => block.lessons.map(e => e.score).sum()).sum() },
    totalScoreMax() { return this.blocks.map(block => block.lessons.map(e => e.scoreTotal).sum()).sum() },
    mixedColors() { 
      let arr = []
      for(let i = 0; i < 10; i++) arr.push(this.mix3(10*i))
      return [...arr, "#0002"]
    }
  },
  methods: {
    launchQuiz(blockName, blockId, lessonName, lessonId) {
      localStorage.setItem('blockName', blockName)
      localStorage.setItem('lessonName', lessonName)
      localStorage.setItem('blockId', blockId)
      localStorage.setItem('lessonId', lessonId)
      this.$router.push("quiz")
    }
  },
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
@import '../styles.scss';
.module-section {
  width: min(70em, 100%);
  margin: auto;
  .module-navbar {
    display: flex;
    align-items: center;
    gap: .5em;
    margin-bottom: max(7em, 15vmin);
    font-size: .9em;
    color: #fff;
    *:last-child { margin-left: auto }
  }
  .module-course {
    margin-bottom: 1.5vmin;
    font-weight: bold;
    color: #fffa;
  }
  .module-name {
    margin-bottom: 4vmin;
    font-family: 'Oswald';
    //font-size: 2.75em;
    font-size: min(2.5em, 7vmin);
    //padding-right: 1em;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #fff;
  }
  .bar-wrapper {
    height: .3em;
  }
}
.block-section {
  width: min(70em, 100%);
  margin: auto;
  margin-bottom: 3vmin;
  border-radius: 4px;
  background: #f3f3f3;
  box-shadow: 0 2px 8px rgba(0,0,0,.12);
}
.block-title {
  display: flex;
  align-items: center;
  gap: 20px;
  line-height: 1.6em;
  padding: 1.75em 2.5em;
  .block-name {
    font-size: 1.15em;
    font-weight: bold;
    text-transform: uppercase;
  }
  .material-icons {
    margin-left: auto;
    font-size: 2em !important;
  }
}
.block-line {
  height: .2em;
  background: #0002;
}
</style>