<template>
<div class="relative elastic">
  <svg viewBox="0 0 110 110" width="100%" height="100%">
    <g v-for='i in data.length' :key='i'>
      <circle v-if="data[data.length-i]"
        :style='{
          strokeDasharray: percentages[data.length-i]*Math.PI +" 314", 
          strokeDashoffset: -partials[data.length-i]*Math.PI,
          strokeWidth: thickness,
          stroke: colors[data.length-i],
        }' 
        r=50 cx=55 cy=55>
      </circle>
    </g>
  </svg>
  <div class="centered">
    <slot/>
  </div>
</div>

</template>

<script>
export default {
  props: {
    data: Array,
    colors: Array,
    thickness: {
      type: Number,
      required: false,
      default: 6
    }
  },
  computed: {
    percentages() { return this.data.map(e => Math.ceil(e / this.data.sum() * 100)) },
    partials() { return [0, ...this.percentages.partialSums()] }
  }
}
</script>

<style lang="scss" scoped>
circle {
  fill: none;
  stroke-linecap: round;
  &:hover { stroke-width: 8px;  }
}
</style>