<template>
  <div class="lesson-block">
    <div class="lesson-header">
      <div class="lesson-type">Урок</div>
      <div class="lesson-index">{{ lesson.blockIndex +"."+ lesson.index }}</div>
      <div class="bar-wrapper">
        <bar-horizontal :data="[
          lesson.score, 
          lesson.scoreMax-lesson.score, 
          lesson.scoreTotal-lesson.scoreMax]" 
          :colors="[mix3(100),mix3(0),'#0002']"/>
      </div>
    </div>
    <div class="lesson-block-body">
      <div class="">
        <span class="material-icons-outlined mobile" v-if="lesson.completed" :style='{ color: tagBackground }'>{{ 'done' }}</span>
        <span class="material-icons-outlined mobile" v-if="lesson.started && !lesson.completed" style='color:#0004'>{{ 'schedule' }}</span>
        <span class="material-icons-outlined mobile" v-if="!lesson.started" style='color:#0002'>{{ 'play_circle' }}</span>
        <span class="tag1" v-if="lesson.completed" :style='{ background: tagBackground }'>
          <span class="material-icons-outlined" v-if="lesson.started">{{ 'done' }}</span>
          Пройдено
        </span>
        <span class="tag1" :class="lesson.started ? 'lesson-started' : 'lesson-not-started'" v-else>
          <span class="material-icons-outlined" v-if="lesson.started">{{ 'schedule' }}</span>
          {{ lesson.started ? 'У процесі' : 'Не розпочато' }}
        </span>
        <span class="lesson-name">&emsp;{{ lesson.name }}</span>
      </div>
      <div class="lesson-info">
        <span :style='{ color: "#0008" }'>
          {{ lesson.tasksCompleted }}&thinsp;/&thinsp;{{ lesson.tasksTotal }} завдань&ensp;&bull;&ensp;
          {{ lesson.score }}&thinsp;/&thinsp;{{ lesson.scoreTotal }} балів</span>
      </div>
    </div>
  </div>
</template>

<script>
import chartDoughnut from '../components/charts/chart-doughnut.vue'
import barHorizontal from '../components/bar-horizontal.vue'
export default {
  components: { chartDoughnut,  barHorizontal },
  props: {
    lesson: Object,
  },
  data() { return {
    tagBackground: this.lesson.tasksCompleted ? this.mix3(Math.trunc(this.lesson.percentage/10)*10) : "#0005",
    tagIconColor: this.lesson.started ? '#0004' : 'transparent',
  }}
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
.lesson-block {
  display: flex;
  align-items: center;
  padding: min(1.75vmax, 20px) 1.75em;
  gap: 1.5em;
  background: #fffa;
  box-sizing: border-box;
  border-bottom: 1px solid #00000015;
  &:hover { background: #00000002; }
  @media (max-width: 420px) { padding: 1.25em 1.75em; }
}
.lesson-header {
  .lesson-type {
    margin: auto;
    font-size: .85em;
    text-align: center;
    text-transform: uppercase;
  }
  .lesson-index {
    margin-bottom: .25em;
    text-align: center;
    font-family: "Oswald";
    font-size: 1.75em;
    line-height: 1.5em;
    font-weight: bold;
    color: #333;
    letter-spacing: 1px;
  }
  .bar-wrapper {
    height: 3px;
    width: 3em;
  }
}
.lesson-block-body {
  flex-grow: 1;
  .tag1 { @media (max-width: 480px) { display: none } }
  .mobile { @media (min-width: 480px) { display: none } }
}
.lesson-name {
  font-weight: 500;
  overflow: hidden;
  line-height: 1.5em;
}
.lesson-started {
  background: #0004;
  color: white;
}
.lesson-not-started {
  color: #333c;
  background: transparent;
  border: 1px solid #00000020;
}
.lesson-info {
  margin-top: 5px;
  gap: .5em;
  color: #0008;
  font-size: .9em;
  line-height: 1.5em;
}
.material-icons-outlined {
  margin-left: -.15em;
  font-size: 1.25em !important;
  font-weight: 300 !important;
  vertical-align: text-bottom;
  color: white;
}
</style>